<template>
  <div class="box">
		<h3>企业名称：{{goodsDetail.id}}</h3>
		<p>所属商品计划：{{goodsDetail.ptGoodsPlanId}}</p>
		<p>商品名称：{{goodsDetail.name}}</p>
		<p>商品简介：{{goodsDetail.平安安享一生产品是由中国平安保险集团推出的百万健康险服务}}</p>
		<p>
      <span>商品生效时间：{{goodsDetail.beginTime}}</span>
      <span>商品失效时间：{{goodsDetail.endTime}}</span>
    </p>
		<h3>产品绑定优加服务：</h3>
    <!-- <el-checkbox-group 
    v-model="checkedValue">
    <el-checkbox v-for="userve in userveList" :label="userve" :key="userve">{{goodsDetail.userve}}</el-checkbox>
  </el-checkbox-group> -->
		<!-- <h3>押金垫付设置</h3>
		<p>服务区域：</p>
		<p>服务次数：{{goodsDetail.不限}}</p>
		<p>等待期：{{goodsDetail.6个月}}</p> -->
		<h3>修改人：{{goodsDetail.operatorName}}</h3>
    <p>修改时间： {{goodsDetail.updateTime}}</p>
		<p v-if="operatorType === '修改'">修改原因：{{goodsDetail.reason}}</p>
		<p v-else>下架原因：{{goodsDetail.reason}}</p>
		<p>相关附件：</p>
		<div>
			<el-button type="success" class="btn" @click="done(true)">同意</el-button>
			<el-button type="warning" plain class="btn" @click="done(false)">拒绝</el-button>
		</div>
  </div>
</template>

<script>
const queryString = require('querystring')
export default {
  name: 'comInfo',
  data() {
    return {
      checkedValue: ['押金垫付'],
      userveList: ['押金垫付', '电话医生'],
      operatorType: this.$route.query.operatorType,
      goodsDetail: {}
    }
  },
  created() {
    this.getDetail()
  },
  methods: {
    getDetail() {
			this.id = this.$route.query.id
			this.axios.get(`/goods/${this.id}/detail`).then((res) => {
              const code = Number(res.data.code)
              if (code === 200) {
									this.goodsDetail = res.data.data.modified
              } else {
                  this.$message(this.mes.busy)
              }
            }).catch((err) => {
              console.log(err)
              this.$message(this.mes.busy)
            })
		},
    done(status) {
      const params = {
        id: this.id,
        confirm: status
      }
      this.axios.post(`/goods/${this.id}/detail/confirm` , queryString.stringify(params)).then((res) => {
              const code = Number(res.data.code)
              if (code === 200) {
                  this.$message(this.mes.success)
                  this.$router.push('infoRecheck?activeName=2')
              } else {
                  this.$message(this.mes.busy)
              }
            }).catch((err) => {
              console.log(err)
              this.$message(this.mes.busy)
            })
    }
  }
}
</script>

<style lang="less" scoped>
.box{
	padding-left: 10px
}
 .btn{
    margin-left: 20px;
  }
</style>
